html,
body,
#app {
  margin: 0;
  padding: 0;
  font-family: Helvetica;
  font-size: 14px;
  font-weight: 100;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  overflow: hidden;
  /* by default, react-native crops content */
}
span {
  display: inline-block;
}
#app .view {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
}
#app .view.pointer-events-none,
#app .view.pointer-events-box-none,
#app .view.pointer-events-box-only * {
  pointer-events: none;
}
#app .view.pointer-events-box-none *,
#app .view.pointer-events-box-only {
  pointer-events: all;
}
#app .text {
  display: inline-block;
}
#app .text.suppress-highlighting {
  user-select: none;
}
#app .text-input {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
  font-family: Helvetica;
  font-size: 14px;
  font-weight: 100;
  background: transparent;
  outline: 0 none;
}
#app .text-input.text-align-start {
  text-align: left;
}
#app .text-input.text-align-center {
  text-align: center;
}
#app .text-input.text-align-end {
  text-align: right;
}
#app .image {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
#app .image.resize-mode-cover {
  background-size: cover;
}
#app .image.resize-mode-contain {
  background-size: contain;
}
#app .image.resize-mode-stretch {
  background-size: 100% 100%;
}
#app .image.resize-mode-none {
  background-size: none;
}
#app .scroll-view {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
  flex-shrink: 1;
}
#app .scroll-view.horizontal {
  overflow-x: auto;
}
#app .scroll-view.horizontal > * {
  flex-direction: row;
}
#app .scroll-view.vertical {
  overflow-y: auto;
}
#app .picker-ios {
  box-sizing: border-box;
  position: relative;
  border: 0 solid black;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
  flex: 0 0 auto;
}
